html {
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  font-family: 'Open Sans', sans-serif;
  background-image: url("backgroundimage.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.container {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  width: 1000px;
  margin: 0 auto;
  margin-top: 70px;
  padding: 50px;
  border-radius: 20px;
  box-shadow: 0 0 20px 0 #b3bbc673;
  background-color: #ffffff;
}

@media (max-width: 912px) {
  .container {
      max-width: 95%;
      padding: 0;
  }
}

@media (min-width: 1024px) {
  .container {
      max-width: 95%;
      padding: 0;
  }
}

.header {
  color: #5072A7;
  font-weight: normal;
  text-align: center;
  padding: 50px;
}

@media (max-width: 912px) {
  .header {
      padding: 40px;
  }
}

.form__select--container {
  display: flex;
  justify-content: space-around;
  padding: 10px;
}

.form__select {
  flex-basis: 350px;
  padding: 25px;
  border-radius: 5px;
}

@media (max-width: 912px) {
  .form__select {
      width: 100px;
      margin: 5px;
  }
}

.form__input--container {
  display: flex;
  justify-content: space-around;
  padding: 10px;
}

.form__input {
  flex-basis: 350px;
  padding: 25px;
  border-radius: 5px;
}

@media (max-width: 912px) {
  .form__input {
      width: 100px;
      margin: 5px;
  }
}

.form__input--grey {
  background-color: #F1F2F5;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.42);
}

.form__button--container {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.form__button {
  width: 80%;
  background-color: #5072A7;
  border: none;
  border-radius: 5px;
  color: white;
  padding: 15px;
}

.form__button:hover {
  background-color: #537bbb;
}

.form__button:active {
  background-color: #4773b9;
}